.Root {
    position: relative;
    display: flex;
    float: right;
    justify-content: center;
    align-items: center;

    height: 115px;
    margin-top: 18px;
    margin-right: 63px;
    color: var(--gray);
    border-bottom: 2px solid transparent;
    background-color: transparent;
    transition: all .35s ease;

    font-family: "RegularFont", sans-serif;
    font-size: 21pt;
    font-weight: 400;
}

.Root:first-child {
    margin-right: 61px;
}

.Root:hover {
    color: var(--white);
}

.active {
    color: var(--white);
    border-bottom-color: var(--white);
}