.Root {
    position: absolute;
    top: 51px;
    left: 56px;

    width: 497px;
    height: 46px;
    z-index: 5;
}

.MenuTrigger {
    padding: 0;
    width: 100%;
    height: 100%;

    border: none;
    color: var(--white);
    outline: none;
    background-color: transparent;
    transition: color .25s ease;
}

.MenuTrigger:hover, .MenuTrigger:focus {
    color: var(--white);
}

.MenuTriggerText {
    position: relative;
    display: inline-block;
    float: left;

    font-family: "RegularFont", sans-serif;
    font-size: 36px;
}

.MenuTriggerIcon {
    position: relative;
    display: inline-block;
    float: left;

    margin-left: 14px;
    height: 46px;

    transition: all .25s ease;
}

.LessIcon {

}

.Menu {
    position: absolute;
    top: -53px;
    left: -38px;

    padding: 0;
    width: 474px;
    background: var(--bg);
    border-radius: 5px;
    box-shadow: 6px 6px 10px 0 rgba(0, 0, 0, 0.66);
    opacity: 0;
    visibility: hidden;
    transform: translateY(-20px);
    transition: opacity 0.25s ease, transform 0.25s ease, visibility 0.25s;

    list-style: none;
}

.active {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
}