.Root {
    position: relative;
    float: left;
    display: flex;

    height: 100%;
    background-color: #2b2b2b;
    border-radius: 30px;
    border: none;
    outline: none;
    margin-right: 16px;
    align-items: center;

    transition: opacity 0.3s ease;
    opacity: 1;
}

.Disabled {
    opacity: 0.6;
}

.Marker {
    position: relative;
    float: left;

    display: none;
    margin-left: -5px;
    margin-right: -23px;
    transition: all 0.2s ease;
}

.checkedMarker {
    display: inline-block;
}

.Text {
    position: relative;
    float: left;

    margin-right: 17px;
    margin-left: 23px;

    font-family: 'RegularFont', serif;
    font-size: 24px;
    color: #9f9f9f;
}
