.Root {
    display: flex;
    align-items: center;

    width: 100%;
    margin-bottom: 44px;
    margin-top: 38px;
}

.Root:hover span {
    color: white;
}

.Text {
    position: relative;
    float: left;

    margin-left: 38px;
    transition: color 0.3s ease;

    font-family: "RegularFont", sans-serif;
    font-size: 32px;
    color: #9f9f9f;
}

.Checkmark {
    position: absolute;

    left: 406px;
    transition: opacity 0.5s ease;

    opacity: 0;
}

.Checked {
    opacity: 1;
}

