.Root {
    position: absolute;
    display: inline-block;

    width: 350px;
    height: 100%;
    margin-right: 26px;
    background-color: var(--bg);
    transition: color .25s ease;
}

.Root > span {
    position: absolute;
}

.Name {
    position: absolute;
    top: -10px;
    left: -1px;

    color: #9f9f9f;
    font-family: 'ThinFont', sans-serif;
    font-size: 40px;
}

.OptVal {
    top: 69px;

    color: white;
    font-family: "RegularFont", sans-serif;
    font-size: 36px;
}

.RealVal {
    top: 119px;

    color: #9f9f9f;
    font-family: "RegularFont", sans-serif;
    font-size: 36px;
}

.Hidden {
    color: var(--bg) !important;
}

.OptMarker {
    margin-right: 40px;
}

.RealMarker {
    margin-right: 23px;
}

.units {
    font-size: 32px;
}