.Root {
    margin-bottom: 27px;
}

.Root > label {
    transition: color 0.3s ease;
}

.Invalid {
    border: 2px solid #ff5349 !important;
    caret-color: #ff5349 !important;
    color: #ff5349 !important;
}

.InvalidLabel {
    color: #ff5349 !important;
}

.Text {
    color: #9F9F9F;
    text-align: left;
    font-family: "RegularFont", sans-serif;
    font-size: 32px;
    display: inline-block;
    width: 350px;
    margin-left: 50px;
}