.plot-fade-enter {
    opacity: 0;
}

.plot-fade-exit {
    opacity: 1;
}

.plot-fade-enter-active {
    opacity: 1;
}

.plot-fade-exit-active {
    opacity: 0;
}

.plot-fade-enter-active,
.plot-fade-exit-active {
    transition: opacity 150ms;
}

.targetTooltip {
    top: calc(91.1% - 8px) !important;
    font-size: 24px !important;
    padding: 14px 23px !important;
    border-radius: 5px !important;
    opacity: 1 !important;
}

div.plotly-notifier {
    visibility: hidden;
}

g.hovertext {
    filter: url(#plotTooltipShadow);
}

g.lines > path.js-line {
    stroke-linecap: round;
}

rect.nwdrag,
rect.nedrag,
rect.swdrag,
rect.sedrag,
rect.ewdrag,
rect.wdrag,
rect.edrag,
rect.nsdrag,
rect.sdrag,
rect.ndrag {
    display: none !important;
}
