.Root {
    position: absolute;
    top: 0;
    left: 0;

    width: 892px;
    height: 3000px;
    background-color: var(--bg);
    box-shadow: 3px 0 14px 0 black;
    z-index: 8;
}

.Logo {
    position: absolute;
    top: 308px;
    left: 50%;
    transform: translate(-50.1%, 0);
}

.Divider {
    position: absolute;

    top: 500px;

    height: 1px;
    width: 100%;

    background-color: rgba(0, 0, 0, 0.58);
    box-shadow: 0 4px 0px rgb(26,26,26);
}

.TopContainer {
    position: relative;

    width: 100%;
    height: 735px;

    box-shadow: 0 4px 0px rgb(26,26,26);
}

.MiddleContainer {
    position: relative;

    width: 100%;
    height: 90px;
    overflow-y: hidden;
}

.BottomContainer {
    position: relative;

    width: 100%;
    height: 2000px;
    max-height: 2000px;
    overflow-y: hidden;
}
