.Root {
    position: absolute;
    top: 0;
    left: 0;

    width: 4500px;
    height: 3000px;

    opacity: 0; /* For open animation */
    visibility: hidden;
    transform: translateY(-20px);
    transition: opacity 0.25s ease, transform 0.25s ease, visibility 0.25s;

    z-index: 10;
}

.Blocker {
    position: absolute;

    width: 100%;
    height: 100%;

    background-color: var(--bg);
    opacity: .8;
}

.Opened {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
}

.Content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: 640px;
    height: 360px;

    border-radius: 5px;
    background-color: var(--bg);
    box-shadow: 6px 6px 10px 0 rgba(0, 0, 0, 0.66);
    opacity: 1;
}

.Title {
    position: absolute;
    top: 59px;
    left: 58px;

    color: #9f9f9f;
    font-family: "RegularFont", sans-serif;
    font-size: 32px;
}

.Buttons {
    position: absolute;
    top: 265px;
    left: 0;

    width: calc(100% - 4px);
}

.WhiteButton {
    position: relative;
    float: right;
    margin-right: 34px;

    color: #9f9f9f;
    background: #212121;
    border: none;
    outline: none;
    transition: color 0.3s ease;
    font-family: "RegularFont", sans-serif;
    font-size: 32px;
}

.WhiteButton:hover {
    color: white;
}

.RedButton {
    position: relative;
    float: right;
    margin-right: 48px;

    color: #9f9f9f;
    background: #212121;
    border: none;
    outline: none;
    transition: color 0.3s ease;
    font-family: "RegularFont", sans-serif;
    font-size: 32px;
}

.RedButton:hover {
    /*color: #ff4140;*/
    color: white;
}
