.scheme-fade-enter {
  opacity: 0;
}

.scheme-fade-exit {
  opacity: 1;
}

.scheme-fade-enter-active {
  opacity: 1;
}

.scheme-fade-exit-active {
  opacity: 0;
}

.scheme-fade-enter-active,
.scheme-fade-exit-active {
  transition: opacity 150ms;
}

.carousel-initialized {
  width: 3500px;
  height: 1533px;
}

.carousel-dots {
  width: 1000px;
  margin-left: 1720px !important;
}

.carousel-dots li {
  float: left;
  padding: 34px !important;
}

.carousel-dots li button:before {
  transition: all 0.4s ease !important;
}

.carousel-dots li.carousel-dots-active button:before {
  color: #ff4140 !important;
}

.carousel-dots li button:before {
  color: gray !important;
  opacity: 1 !important;
  font-size: 128px !important;
}
