.rec-animation-enter {
  opacity: 0;
  transform: scale(0.9);
}
.rec-animation-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 500ms, transform 500ms;
}
.rec-animation-exit {
  opacity: 1;
}
.rec-animation-exit-active {
  height: 0;
  opacity: 0;
  transition: opacity 100ms, height 400ms ease-out, margin-bottom 400ms ease-out;
}
